.componentView {
    border: solid 1px #000000;
    display: inline-block;
    padding-right: 10px;
    padding-left: 10px;
    padding-bottom: 10px;
    margin-right: 10px;
    vertical-align: top;
}

.inPortsList {
    display: inline-block;
    vertical-align: top;
    border: solid 1px #000000;
    min-width: 20px;
    min-height: 100%;
}

.outPortsList {
    display: inline-block;
    vertical-align: top;
    border: solid 1px #000000;
}

.componentDescription {
    display: inline-block;
    vertical-align: top;
    border: solid 1px #000000;
}

.exportTextArea,.importTextArea {
    width: 100%;
    height: 75%;
    resize: none;
}


